@import "src/common/styles/sass/variables";
@import "src/common/styles/sass/mixin";
.btn{
  border:none;
  @include display(center,center,nowrap);
  @include text($titleFontFamily, 14px, 1.2);
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 8px 20px;
  max-width: 160px;
  width: 100%;
  color: $textColor;
  outline: none;
  position: relative;
  margin: 5px;
  z-index: 0;
  &:after{
    content: '';
    position: absolute;
    width: 50%;
    height: 10px;
    background-color: $primaryColor;
    bottom: 0;
    z-index: -1;
    transition: all 0.2s ease-in-out;
  }
  &:hover{
    text-decoration: none;
    color:$textColor;
    &:after{
      width: 100%;
      height: 110%;
      transition: all 0.3s ease-in-out 0s;
    }

  }
}